import React, {useState} from 'react'
import styles from './Gra.module.css'

export default function Gra() {
    const [color1, setColor1] = useState('#00F260');
    const [color2, setColor2] = useState('#0575E6');

    function AA(e) {
        const {name, value} = e.target;
        if (name === 'color1') {
            setColor1(value)
        }
        if (name === 'color2') {
            setColor2(value)
        }
    }

// ---------------------------------

    function BB(){
        setColor1('#00F260')
        setColor2('#0575E6')
    }

    return (
        <div className={styles.container}
             style={{background: `linear-gradient(75deg,${color1},${color2})`}}>

            <div className={styles.inputGroup}>
                <label>请选择第一个颜色</label>
                <input type="color"
                       value={color1}
                       name='color1'
                       className={styles.inputColor}
                       onChange={AA}
                />
            </div>

            <div className={styles.inputGroup}>
                <label>请选择第二个颜色</label>
                <input type="color"
                       value={color2}
                       name='color2'
                       className={styles.inputColor}
                       onChange={AA}
                />
            </div>
            <button className={styles.button} onClick={BB}>重置为默认</button>
        </div>
    )
}
